.upBtn {
  position: fixed;
  z-index: 5000;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  text-align: center;
  bottom: -50px;
  right: 30px;
  cursor: pointer;
  transition: all .5s ease;
}

.upBtn:hover {
  background-color: black;
}

.upBtn_show {
  bottom: 40px;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.header__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header .logo {
  width: 120px;
}

.header .logo img {
  width: 100%;
}

.header__nav {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.header__nav li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__nav a {
  color: rgba(255, 255, 255, 0.7);
  font-size: 18px;
  text-decoration: none;
  margin: 0 20px;
  line-height: 50px;
}

.header__nav a:hover {
  color: white;
}

@media only screen and (max-width: 600px) {
  .header__container {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .header .logo {
    width: 50px;
    padding: 10px 0 0 15px;
  }
  .header__nav {
    display: none;
    background-color: rgba(0, 0, 0, 0.5);
  }
}

/**
 * Toggle Switch Globals
 *
 * All switches should take on the class `hamburger-button` as well as their
 * variant that will give them unique properties. This class is an overview
 * class that acts as a reset for all versions of the icon.
 */
.hamburger-button {
  display: none;
  background-color: white;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 50px;
  height: 50px;
  font-size: 0;
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  box-shadow: none;
  border-radius: none;
  border: none;
  cursor: pointer;
  transition: background 0.3s;
}

.hamburger-button:focus {
  outline: none;
}

.hamburger-button span {
  display: block;
  position: absolute;
  top: 24px;
  left: 15px;
  right: 15px;
  height: 2px;
  background: white;
}

.hamburger-button span::before,
.hamburger-button span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: white;
  content: "";
}

.hamburger-button span::before {
  top: -9px;
}

.hamburger-button span::after {
  bottom: -9px;
}

/**
 * Style
 *
 * Hamburger to "x" (htx). Takes on a hamburger shape, bars slide
 * down to center and transform into an "x".
 */
.hamburger-button_htx {
  background-color: transparent;
}

.hamburger-button_htx span {
  transition: background 0 0.3s;
}

.hamburger-button_htx span::before,
.hamburger-button_htx span::after {
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0;
}

.hamburger-button_htx span::before {
  transition-property: top, transform;
}

.hamburger-button_htx span::after {
  transition-property: bottom, transform;
}

/* active state, i.e. menu open */
.hamburger-button_htx.active {
  background-color: transparent;
}

.hamburger-button_htx.active span {
  background: none;
}

.hamburger-button_htx.active span::before {
  top: 0;
  transform: rotate(45deg);
}

.hamburger-button_htx.active span::after {
  bottom: 0;
  transform: rotate(-45deg);
}

.hamburger-button_htx.active span::before,
.hamburger-button_htx.active span::after {
  transition-delay: 0, 0.3s;
}

@media only screen and (max-width: 600px) {
  .hamburger-button {
    display: block;
  }
}

.albums {
  position: relative;
}

.albums__album {
  background-size: cover;
}

.albums__album:nth-child(even) .albums__container {
  justify-content: flex-end;
  text-align: right;
}

.albums__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.albums__container {
  min-height: 100vh;
  max-width: 1200px;
  margin: 0 auto;
  padding: 400px 15px 300px;
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: flex-start;
}

.albums__main {
  max-width: 570px;
  color: white;
}

.albums__main h2 {
  font-size: 36px;
  line-height: 52px;
  margin: 0;
  margin-bottom: 20px;
}

.albums__main p {
  font-size: 24px;
  line-height: 42px;
  font-weight: 300;
  margin: 0;
  margin-bottom: 20px;
}

.albums__main a {
  font-size: 24px;
  line-height: 42px;
  font-weight: 300;
  margin: 0;
  margin-bottom: 20px;
}

.albums__arrow {
  position: absolute;
  bottom: 30px;
  left: 50%;
}

.albums__arrow i {
  font-size: 20px;
  font-weight: 300;
  color: white;
}

.footer {
  padding: 100px 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__img {
  width: 100px;
  margin-bottom: 60px;
}

.footer__socials {
  display: flex;
  align-items: center;
}

.footer__socials a {
  margin: 0 20px;
}

.footer__socials i {
  font-size: 25px;
}

