//mixins
@mixin r($width) {
  @media only screen and (max-width: $width+ "px") {
    @content; } }

@mixin rmin($width) {
  @media only screen and (min-width: $width+ "px") {
    @content; } }

// Normalize Styles
@import "node_modules/normalize.css/normalize";

// Import Modules
@import "../_modules/link/link";
@import "../_modules/upBtn/upBtn";
@import "../_modules/header/header";
@import "../_modules/header/hamburger-button";
@import "../_modules/albums/albums";
@import "../_modules/footer/footer";

// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  max-width: 100%;
  font-size: 16px;
  font-family: 'Roboto', sans-serif;
  background: url("../images/backgrounds/1600xxx.jpg") no-repeat 50% 0% fixed;
  background-size: cover; }

.yeogurt-info {
  text-align: center;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto; }

.version {
  font-size: 14px;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  position: absolute;
  bottom: 10px;
