.footer {
	padding: 100px 0;
	background-color: rgba(black, .4);
	&__container {
		display: flex;
		flex-direction: column;
		align-items: center; }
	&__img {
		width: 100px;
		margin-bottom: 60px; }
	&__socials {
		display: flex;
		align-items: center;
		a {
			margin: 0 20px; }
		i {
			font-size: 25px; } } }
