.upBtn {
	position: fixed;
	z-index: 5000;
	color: white;
	background-color: rgba(black, .5);
	width: 40px;
	height: 40px;
	line-height: 40px;
	border-radius: 50%;
	text-align: center;
	bottom: -50px;
	right: 30px;
	cursor: pointer;
	transition: all .5s ease;
	&:hover {
		background-color: rgba(black, 1); }
	&_show {
		bottom: 40px; } }
