.albums {
	position: relative;
	&__album {
		background-size: cover;
		&:nth-child(even) {
			.albums__container {
				justify-content: flex-end;
				text-align: right; } } }
	&__overlay {
		width: 100%;
		height: 100%;
		background-color: rgba( black, .1);
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1; }
	&__container {
		min-height: 100vh;
		max-width: 1200px;
		margin: 0 auto;
		padding: 400px 15px 300px;
		position: relative;
		z-index: 10;
		display: flex;
		justify-content: flex-start; }
	&__main {
		max-width: 570px;
		color: white;
		h2 {
			font-size: 36px;
			line-height: 52px;
			margin: 0;
			margin-bottom: 20px; }
		p {
			font-size: 24px;
			line-height: 42px;
			font-weight: 300;
			margin: 0;
			margin-bottom: 20px; }
		a {
			font-size: 24px;
			line-height: 42px;
			font-weight: 300;
			margin: 0;
			margin-bottom: 20px; } }
	&__arrow {
		position: absolute;
		bottom: 30px;
		left: 50%;
		i {
			font-size: 20px;
			font-weight: 300;
			color: white; } } }
