.header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 1000;
	&__container {
		display: flex;
		flex-direction: column;
		align-items: center; }
	.logo {
		width: 120px;
		img {
			width: 100%; } }
	&__nav {
		width: 100%;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		padding: 0;
		margin: 0;
		border-top: 1px solid rgba(white, .2);
		border-bottom: 1px solid rgba(white, .2);

		li {
			margin: 0;
			padding: 0;
			list-style-type: none; }
		a {
			color: rgba(white, .7);
			font-size: 18px;
			text-decoration: none;
			margin: 0 20px;
			line-height: 50px;
			&:hover {
				color: rgba(white, 1); } } } }

@include r(600) {
	.header {
		&__container {
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;
			align-items: flex-start;
			background-color: rgba(black, .5); }
		.logo {
			width: 50px;
			padding: 10px 0 0 15px; }
		&__nav {
			display: none;
			background-color: rgba(black, .5); } } }
